import * as React from 'react';
import * as BABYLON from 'babylonjs';
import Scene, { SceneEventArgs } from './Scene'; // import the component above linking to file we just created.
import 'babylonjs-loaders';
import 'babylonjs-materials';

export class Playground extends React.Component<{}, {}> {
    onSceneMount = (e: SceneEventArgs) => {
        const { canvas, scene, engine } = e;

        // This creates and positions a free camera (non-mesh)
        var camera = new BABYLON.FreeCamera("camera1", new BABYLON.Vector3(0, 5, -10), scene);

        // This targets the camera to scene origin
        camera.setTarget(BABYLON.Vector3.Zero());

        // This attaches the camera to the canvas
        camera.attachControl(canvas, true);

        scene.ambientColor = new BABYLON.Color3(0.3, 0.3, 0.3);

        var light = new BABYLON.DirectionalLight("dir01", new BABYLON.Vector3(2, 1, -1), scene);

        var envTexture = new BABYLON.CubeTexture("/scenes/SpecularHDR.dds", scene);
        scene.createDefaultSkybox(envTexture, true, 1000);
        /* var skyMaterial = new BABYLON.SkyMaterial("skyMaterial", scene);
        skyMaterial.backFaceCulling = false;

        var skybox = BABYLON.Mesh.CreateBox("skyBox", 1000.0, scene);
        skybox.material = skyMaterial; */

        BABYLON.SceneLoader.ImportMesh("", "scenes/", "environment.glb", scene, function (meshes) {
            scene.createDefaultCameraOrLight(true, true, true);
            // scene.createDefaultEnvironment();
            var generator = new BABYLON.ShadowGenerator(512, light);
            generator.useBlurCloseExponentialShadowMap = true;
            generator.useKernelBlur = true;
            generator.blurScale = 1.0;
            generator.blurKernel = 10.0;

            for (var index = 0; index < scene.meshes.length; index++) {
                var m = meshes[index];

                m.receiveShadows = true;
                generator.getShadowMap().renderList.push(m);
                generator.useBlurExponentialShadowMap = true;
            }

        });


        engine.runRenderLoop(() => {
            if (scene) {
                scene.render();
            }
        });
    }

    render() {
        return (
            <div>
                <Scene onSceneMount={this.onSceneMount} width={1000} height={1000} />
            </div>
        )
    }
}