class VariableState {

    private static basicTypes = [
        'bool', 'bool*',
        'int', 'int*',
        'char', 'char*',
        'float', 'float*',
        'double', 'double*',
        'long', 'long*'
    ];

    name: string;
    type: string;
    value: string;

    constructor(v: Partial<VariableState>) {
        this.name = v.name;
        this.type = v.type;
        this.value = v.value;
    }

    hasBasicType(): boolean {
        return VariableState.basicTypes.indexOf(this.type) > -1 && this.name !== 'endl';
    }

    isArray(): boolean {
        let isArrayRegExp = new RegExp(
            `(${VariableState.basicTypes.map(x => x.replace('*', '*')).join('|')})(\[[0-9]+\])+`
        );

        return isArrayRegExp.test(this.type);
    }

    dimensions(): number {
        if (!this.isArray()) {
            return 0;
        }
        return (this.value.match(/[[0-9]]+/g) || []).length;
    }
}

export {VariableState};