import * as React from 'react';
import Navbar from './Navbar';
import Footer from './Footer';
import { StrapiClient } from '../StrapiClient';
import { ToastContainer, toast, ToastOptions } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { BrowserRouter as Router, RouteComponentProps } from 'react-router-dom';

const LoginPage = ({ title, history }) =>  (
    <>
        <Navbar/>
        <main>
            <section className="section section-shaped section-lg my-0">
                <div className="shape shape-style-1 bg-gradient-default">
                    <span/>
                    <span/>
                    <span/>
                    <span/>
                    <span/>
                    <span/>
                    <span/>
                    <span/>
                </div>
                
                <div className="container pt-lg-md">
                    <div className="row justify-content-center">
                        <div className="col-lg-5">
                            <div className="card bg-secondary shadow border-0">
                                <div className="card-body px-lg-5 py-lg-5">
                                    <LoginForm history={history} />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <ToastContainer />
        </main>
        <Footer/>
    </>
);

class LoginForm extends React.Component <{} & RouteComponentProps<{}>, {} > {
    
    emailInput: HTMLInputElement;
    passwordInput: HTMLInputElement;

    async handleLogin(e) {
        e.preventDefault();
        try {
            var ans = await StrapiClient.login(this.emailInput.value, this.passwordInput.value);
            this.props.history.push('/dashboard');
        } catch (err) {
            toast.error(err.toString(), {
                position: toast.POSITION.BOTTOM_CENTER,
                autoClose: 3000,
                className: 'text-center',
              } as ToastOptions);
        }
    }
    
    render() {
        return (
            <>
                <div className="text-center text-muted mb-4">
                    <small>Sign in with credentials</small>
                </div>
                <form role="form" onSubmit={e => this.handleLogin(e)}>
                    <div className="form-group mb-3">
                        <div className="input-group input-group-alternative">
                            <div className="input-group-prepend">
                                <span className="input-group-text">
                                    <i className="fas fa-at"/>
                                </span>
                            </div>
                            <input
                                className="form-control"
                                placeholder="Email"
                                ref={input => this.emailInput = input}
                            />
                        </div>
                    </div>
                    <div className="form-group">
                        <div className="input-group input-group-alternative">
                            <div className="input-group-prepend">
                                <span className="input-group-text">
                                    <i className="fas fa-key"/>
                                </span>
                            </div>
                            <input
                                className="form-control"
                                placeholder="Password"
                                type="password"
                                ref={input => this.passwordInput = input}
                            />
                        </div>
                    </div>
                    <div className="custom-control custom-checkbox">
                        <input className="custom-control-input" id="customCheckLogin" type="checkbox"/>
                        <label className="custom-control-label" htmlFor=" customCheckLogin">
                            <span>Remember me</span>
                        </label>
                    </div>
                    <div className="text-center">
                        <button type="submit" className="btn btn-primary my-4">
                            Sign in
                        </button>
                    </div>
                </form> 
                <div className="row mt-3">
                    <div className="col-6">
                        <a href="#" className="text-light">
                            <small>Forgot password?</small >
                        </a>
                    </div>
                    <div className="col-6 text-right">
                        <a href="#" className="text-light">
                            <small>Create new account</small>
                        </a>
                    </div>
                </div>
            </>
        );
    }
}

export default LoginPage;