import * as React from 'react';
import AppStyle from '../AppStyle';
import { Animated } from 'react-animated-css';
import { CounterExample } from '../models';

function Title(props: {children: string | JSX.Element}) {
    return (
        <h3 className="text-center m-1 p-2 mb-2 font-weight-bold mb-2">
            {props.children}
        </h3>
    );
}

function Description(props: {children: string | JSX.Element}) {
    return (
        <div
            className="description m-1 mb-2 p-2 pb-3 font-weight-bold rounded shadow"
            style={{
                background: AppStyle.purpleGradient,
                color: '#ffffff'
            }}
        >
            {props.children}
        </div>
    );
}

function ActiveStep(props: {children: string | JSX.Element}) {
    return (
        <div
            className="description m-1 mb-3 p-2 font-weight-bold rounded shadow"
            style={{
                background: AppStyle.orangeGradient,
                color: '#ffffff'
            }}
        >
            {props.children}
        </div>
    );
}

function CompletedStep(props: {children: string | JSX.Element}) {
    return (
        <div
            className="description m-1 mb-2 p-2 font-weight-bold rounded"
            style={{
                background: '#D8D8D8',
                color: '#727272',
                borderLeft: '0.5em solid #14EA08'
            }}
        >
            {props.children}
        </div>
    );
}

function Example(props: {counterExample: CounterExample, onClick?: (counterExample: CounterExample) => void}) {
    return (
        <Animated
            animationIn="fadeIn"
            animationOut="fadeOut"
            isVisible={true}
            animateOnMount={true}
        >
            <div
                className="row bg-white rounded shadow text-center p-3 m-1 mb-2 font-weight-bold"
                style={{color: '#727272'}}
                onClick={() => props.onClick(props.counterExample)}
            >
                <div className="col h-100 p-0">
                    <div className="w-100 border-bottom border-black">Exemplu</div>
                    <div className="p-1" style={{whiteSpace: 'nowrap', overflow: 'auto'}}>
                        {props.counterExample.counterExampleStr}
                    </div>
                </div>
                <div className="col h-100 p-0">
                    <div className="w-100 border-bottom border-black">Curent</div>
                    <div className="p-1" style={{whiteSpace: 'nowrap', overflow: 'auto'}}>
                        {props.counterExample.current}
                    </div>
                </div>
                <div className="col h-100 p-0">
                    <div className="w-100 border-bottom border-black">Corect</div>
                    <div className="p-1" style={{whiteSpace: 'nowrap', overflow: 'auto'}}>
                        {props.counterExample.expected}
                    </div>
                </div>
            </div>
        </Animated>
    );
}

function ChallengeCompleted(props: {children: string | JSX.Element}) {
    return (
        <Animated
            animationIn="fadeIn"
            animationOut="fadeOut"
            isVisible={true}
            animateOnMount={true}
        >
            <div
                className="description m-1 mb-2 p-2 pb-3 font-weight-bold rounded shadow"
                style={{
                    background: AppStyle.greenGradient,
                    color: '#ffffff'
                }}
            >
                {props.children}
            </div>
        </Animated>
    );
}

export {ActiveStep, CompletedStep, ChallengeCompleted, Description, Example, Title};