import * as React from 'react';
import AppStyle from '../AppStyle';

interface ConsolePanelProps {
    consoleOutput: string;
    onConsoleInputChanged: (consoleInput: string) => void;
}

class ConsolePanel extends React.Component < ConsolePanelProps, {} > {
    inputArea: HTMLTextAreaElement;
    outputArea: HTMLTextAreaElement;

    constructor(props: ConsolePanelProps) {
        super(props);
    }
    render() {
        return (
            <div
                className="row form-group w-100"
                style={{
                    paddingBottom: '3px',
                    height: '150px',
                    borderRadius: '10px',
                    margin: '10px 0 0 0'
                }}
            >
                <div className="col-6 h-100 rounded-left" style={{background: AppStyle.blueGradient}}>
                    <div className="border-bottom border-white pt-1" style={{height: 35}}>
                        <h5 className="text-left" style={{color: AppStyle.white}}>Input</h5>
                    </div>
                    <textarea
                        className="w-100 font-weight-bold"
                        style={{
                        resize: 'none',
                        height: 'calc(100% - 35px)',
                        background: 'transparent',
                        border: 'none',
                        color: AppStyle.white,
                        fontSize: '0.75em'
                    }}
                        ref={(elem) => this.inputArea = elem}
                        onChange={(event) => this.props.onConsoleInputChanged(event.target.value)}
                    />
                </div>
                    
                <div className="col-6 h-100 rounded-right" style={{background: AppStyle.greenGradient}}>
                    <div className="border-bottom border-white pt-1" style={{height: 35}}>
                        <h5 className="text-right" style={{color: AppStyle.white}}>Output</h5>
                    </div>
                    <textarea
                        className="w-100 text-right font-weight-bold"
                        style={{
                        resize: 'none',
                        height: 'calc(100% - 35px)',
                        background: 'transparent',
                        border: 'none',
                        color: AppStyle.white,
                        fontSize: '0.75em'
                    }}
                        ref={(elem) => this.outputArea = elem}
                        value={this.props.consoleOutput}
                        readOnly={true}
                    />
                </div>
            </div>
        );
    }
}

export default ConsolePanel;
