const JSCPP = require('jscpp_patched');
import { JSCPPResult, VariableState, ASTNode } from '../models';

/*
 * Retrieves the latest value from the CPP runtime.
 * If the source code doesn't compile, error is thrown.
 * If the code compiles but the variable name doesn't exist, returns null.
 */
function getVariableValue(varName: string, cppCode: string) {
  let varValue = null;
  let cppDebugger = JSCPP.run(
    cppCode,
    '',
    { debug: true, stdio: {write: function() {/* we do nothing */}}}
  );
  let done = cppDebugger.next();

  while (!done) {
    let variables = cppDebugger.variable();
    let targetVar = variables.find(x => x.name === varName);
    if (targetVar) {
      varValue = targetVar.value;
    }
    done = cppDebugger.continue();
  }

  if (typeof varValue === 'string') {
    if (varValue.startsWith('[') && varValue.endsWith(']')) {
      // cast as array
      return JSON.parse(varValue);
    }
    // the pattern is \"value\""
    return varValue.slice(1, varValue.length - 1);
  }
  return varValue;
}

/* 
* Evaluates a CPP expression from a source code.
*/
function evaluate(cppExp: string, valueType: string, sourceCode: string, context: string = '') {
  // we take the main code and we append a new line just before the return 0
  // return 0 is mandatory in our case
  var mainMatch = sourceCode.match(
    /int[\n\r\s]+main[\n\r\s]*\([\n\r\s]*\)[\n\r\s]*{[^]*(return[\n\r\s]+0;)[^]*}/gm
  );
  // TODO: might not be necessary, maybe main is not mandatory
  if (!mainMatch) {
    return null;
  }
  const mainCode: string = mainMatch[0];
  const injectedMainCode = mainCode.replace(
    /return([\n\r\s]+)0;/,
    `${context}\n ${valueType} __eval_result__ = ${cppExp};\n return 0;`
  );
  return getVariableValue('__eval_result__', sourceCode.replace(mainCode, injectedMainCode));
}

/*
* Compiles and runs the CPP source into debug mode and accumulates all intermediate states.
*/
function compileAndGetVariableStates(code: string, input: string, maxSteps: number = 1000): JSCPPResult  {
  let output = '';  
  try {
      
      let cppDebugger = JSCPP.run(code, input, {
        debug: true,
        stdio: {
          write: s => {
            output += s;
          }
        }
      });

      cppDebugger.stopConditions = {
        isStatement: true,
        positionChanged: true,
        lineChanged: true
      };
      let newStates: VariableState[][] = [];
      let astNodesHistory: ASTNode[] = [];
      let done = cppDebugger.next();
      let line = 0;
      while (!done) {
        line++;
        if (line > maxSteps) {
          throw 'Time limit exceeded';
        }
        // get all variables with .variable() method
        newStates.push(cppDebugger.variable().map(v => new VariableState(v)));
        if (cppDebugger.prevNode) {
          astNodesHistory.push(cppDebugger.prevNode);
        }
        done = cppDebugger.continue();
      }
      // no exceptions so far, we update existing states
      /* this.variableStates = newStates;
      // we update line history too
      this.lineHistory = newLineHistory;
      // set the slider to the lastest state index
      this.slider.max = (newStates.length - 1).toString();
      this.slider.value = (newStates.length - 1).toString();
      this.setCurrentVariableState();
      // set console output
      this.consolePanel.outputArea.value = output;
      this.setState({error: null}); */
      return {
        variableStates: newStates,
        astNodesHistory: astNodesHistory,
        output: output
      };
    } catch (err) {
      let formattedErr = err.replace(/(\\t|\\n|\\r|ERROR\:)/g, '');
      if (formattedErr.indexOf('---') > 1) {
        formattedErr = formattedErr.substr(0, formattedErr.indexOf('---'));
      }
      formattedErr = formattedErr.replace('Parsing Failure', 'Compile Error');
      return {
        compileError: formattedErr,
        output: output
      };
    }
}

/*
* Returns an array as string which can be initialized in a CPP code.
*/
function cppArray(arr: any[]): string {
  return `{${arr.toString()}}`;
}

/*
* Returns a string which can be initialized in a CPP code.
*/
function cppString(str: string): string {
  return `"${str}"`;
}

/*
* Runs a CPP source and returns output from console.
*/
function getConsoleOutput(cppCode: string, consoleInput: string = ''): string {
  let output = '';
  JSCPP.run(
    cppCode,
    consoleInput,
    {
      stdio: {
        write: function(str: string) {
          output += str;
        }
      }
    }
  );
  return output;
}

export interface FunctionParameter {
  parameterName: string;
  parameterType: string;
  dimensions: number;
}

interface FunctionHeader {
  functionName: string;
  returnType: string;
  parameters: FunctionParameter[];
}
/*
* Given a function name, extract the signature (return type, parameters).
*/
function getCPPFunctionSignature(functionName: string, cppSource: string): FunctionHeader {
  let regex = `([A-Za-z0-9_]+)[\\n\\r\\s]*${functionName}[\\n\\r\\s]*\((.|\\n|\\s|\\r)*?\)[\\n\\r\\s]*{`;
  let match = cppSource.match(regex);
  let paramMatches = match[2].replace('(', '').replace(')', '').split(',');
  let parameters = [];
  for (let paramMatch of paramMatches) {
    let paramType = '';
    let paramName = '';
    let foundParamType = false;
    let foundParamName = false;
    for (let chr of paramMatch) {
      if (chr === ' ' || chr === '\n' || chr === '\r' || chr === '\t') {
        if (paramType.length > 0) {
          foundParamType = true;
        }
        continue;
      }
      if (!foundParamType) {
       paramType += chr;
      } else if (!foundParamName && chr === '[') {
        foundParamName = true;
        paramType += chr;
      } else if (!foundParamName) {
        paramName += chr;
      } else if (foundParamName) {
        if (chr === '[' || chr === ']' || (chr >= '0' && chr <= '9')) {
         paramType += chr;
        }
      }
    }
    parameters.push({
      parameterName: paramName,
      parameterType: paramType,
      dimensions: paramType.split('').filter(x => x === '[').length
    });
  }
  return {
    functionName: functionName,
    parameters: parameters,
    returnType: match[1]
  };
}

export {
  getVariableValue,
  compileAndGetVariableStates,
  evaluate,
  cppArray,
  cppString,
  getConsoleOutput,
  getCPPFunctionSignature
};