import * as React from 'react';
import './LandingPage.css';
import Navbar from './Navbar';
import FirstPage from './FirstPage';
import FeatureCards from './FeatureCards';
import FeatureList from './FeatureList';
import PromoPage from './PromoPage';
import ContactForm from './ContactForm';
import StripedPage from './StripedPage';
import Press from './Press';
import Footer from './Footer';
import { ToastContainer, toast, ToastOptions } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import MessengerCustomerChat from 'react-messenger-customer-chat';

class LandingPage extends React.Component<{}, {}> {
  componentDidMount() {
    if (localStorage.getItem('cookie-consent') === 'accept') {
      return;
    }
    let acceptCookies = () => {
      localStorage.setItem('cookie-consent', 'accept');
    };
    toast.info('🍪 Folosim cookie-uri pentru a îmbunătăți experiența.', {
      position: toast.POSITION.BOTTOM_CENTER,
      autoClose: false,
      className: 'text-center',
      onClose: acceptCookies
    } as ToastOptions);
  }
  render() {
    return (
      <div>
        <Navbar />
        <main>
          <FirstPage />
          <FeatureCards />
          <FeatureList />
          <PromoPage />
          <StripedPage />
          {/* <ContactForm /> */}
          {/* <Press /> */}
          <ToastContainer />
          <MessengerCustomerChat pageId="emunromania" appId="331162497410269" />
        </main>
        <Footer />
      </div>
    );
  }
}

export default LandingPage;
