import './Editor.css';
import * as React from 'react';
import MonacoEditor from 'react-monaco-editor';
import { RouteComponentProps } from 'react-router-dom';
import AppStyle from '../AppStyle';
import Navbar from '../components/Navbar';
import LivePanel from './LivePanel';
import TaskPanel from './TaskPanel';
import SplitPane from 'react-split-pane';
import { Challenges } from '../challenges';

import {
  Challenge,
  CounterExample,
  Course,
  JSCPPResult,
  VariableState,
} from '../models';
import { compileAndGetVariableStates } from './JSCPPUtils';
import { evaluateTask } from './EvalUtils';

interface ChallengeInfo {
  id: string;
}

interface EditorProps {
  course?: Course;
  challenge?: Challenge;
}

interface EditorState {
  code: string;
  challenge?: Challenge;
  JSCPPResult: JSCPPResult;
  sliderIndex: number;
  currentTaskIndex: number;
  codeLensRange?: monaco.Range;
  errorMessage?: string;
  prevVariables: VariableState[];
  consoleInput: string;
  counterExamples: CounterExample[];
}

class Editor extends React.Component<
  EditorProps & RouteComponentProps<ChallengeInfo>,
  EditorState
> {
  editor: MonacoEditor.editor;
  monaco: MonacoEditor;
  livePanel: LivePanel;
  codeLens: monaco.IDisposable;

  constructor(props: EditorProps) {
    super(props);

    let workspaceCode = window.localStorage.getItem('workspace_code');
    if (!workspaceCode) {
      workspaceCode =
        '#include <iostream>\nusing namespace std;\n\nint main() {\n\tint x = 23;\n\treturn 0;\n}';
    }
    let challenge = Challenges.find(
      x =>
        x.title.toLowerCase().replace(' ', '-') === this.props.match.params.id
    );
    if (!challenge && this.props.match.params.id) {
      this.props.history.push('/dashboard');
    }
    let initialCode = challenge ? challenge.cppCode : workspaceCode;
    let firstResult = compileAndGetVariableStates(initialCode, '');
    this.state = {
      challenge: challenge,
      code: initialCode,
      currentTaskIndex: 0,
      codeLensRange: null,
      JSCPPResult: firstResult,
      errorMessage: firstResult.compileError,
      sliderIndex: firstResult.compileError
        ? 0
        : firstResult.variableStates.length - 1,
      prevVariables: [],
      consoleInput: '',
      counterExamples: [],
    };
  }

  isWorkspace(): boolean {
    return !this.props.match.params.id;
  }

  editorDidMount = (editor: monaco.editor.IEditor, monaco: MonacoEditor) => {
    editor.focus();
    this.monaco = monaco;
    this.editor = editor;
    this.editor.getModel().setValue(this.state.code);
    monaco.editor.defineTheme('customTheme', {
      base: 'vs-dark',
      inherit: true,
      rules: [{ background: AppStyle.darkGray.substr(1) }],
      colors: {
        'editor.background': AppStyle.darkGray,
        'editor.lineHighlightBackground': '#0000FF20',
        'editorLineNumber.foreground': '#50515B',
        'editor.inactiveSelectionBackground': AppStyle.darkBlue,
        'editor.selectionHighlightBackground': 'yellow',
        'editorCodeLens.foreground': '#50515B',
      },
    });
    monaco.editor.setTheme('customTheme');
    // load first examples
    this.onRunClick();
  }

  setCodeLensTextOnFunction(functionName: string, text: string) {
    if (this.codeLens) {
      this.codeLens.dispose();
    }
    this.codeLens = monaco.languages.registerCodeLensProvider('cpp', {
      provideCodeLenses: (model: any, token: any) => {
        let range = this.findFunctionStartLine(functionName);
        return [
          {
            range: {
              startLineNumber: range.startLineNumber,
              endLineNumber: range.endLineNumber,
              startColumn: range.startColumn,
              endColumn: range.endColumn,
            },
            id: `codeLens_${functionName}`,
            command: {
              title: text,
              id: null,
            },
          },
        ];
      },
      resolveCodeLens: function({  }: any, codeLens: any, {  }: any) {
        return codeLens;
      },
    });
  }

  updateCurrentVariableStates(
    prevStates: VariableState[],
    newStates: VariableState[]
  ): VariableState[] {
    let commonStates = [];
    let deltaStates = [];
    // O(N^2) algorithm FTW
    // first we find out the common states
    for (let prevState of prevStates) {
      let found = false;
      for (let newState of newStates) {
        if (newState.name === prevState.name) {
          commonStates.push(newState);
          found = true;
          break;
        }
      }
    }
    // we put at the end the new states that haven't been
    // in the previous ones
    for (let newState of newStates) {
      let found = false;
      for (let commonState of commonStates) {
        if (commonState.name === newState.name) {
          found = true;
          break;
        }
      }
      if (!found) {
        deltaStates.push(newState);
      }
    }
    return commonStates.concat(deltaStates);
  }

  onChange() {
    this.livePanel.setConsoleInput(this.state.consoleInput);

    let newResult = compileAndGetVariableStates(
      this.editor.getModel().getValue(),
      this.state.consoleInput
    );
    if (newResult.compileError) {
      this.setState({
        code: this.editor.getModel().getValue(),
        errorMessage: newResult.compileError,
      });
      return;
    }
    this.setState({
      code: this.editor.getModel().getValue(),
      JSCPPResult: newResult,
      sliderIndex: newResult.variableStates.length - 1,
      errorMessage: null,
      prevVariables: this.state.JSCPPResult.variableStates
        ? this.state.JSCPPResult.variableStates[this.state.sliderIndex]
        : [],
    });
    this.livePanel.slider.value = this.state.sliderIndex.toString();
  }

  async setConsoleInput(consoleInput: string) {
    await this.setState({
      consoleInput: consoleInput,
    });
    this.onChange();
  }

  highlightCurrentLine() {
    let currentAST = this.state.JSCPPResult.astNodesHistory[
      this.state.sliderIndex
    ];
    let currentValue = this.state.JSCPPResult.variableStates[
      this.state.sliderIndex
    ];

    if (!currentAST) {
      this.livePanel.setCurrentIdentifier(null);
      return;
    }

    this.livePanel.setCurrentIdentifier(currentAST.Identifier);

    let currentLine = this.editor
      .getModel()
      .getValue()
      .split('\n')[currentAST.sLine - 1];
    let trimmedLine = currentLine.trim();
    let colStart = currentLine.indexOf(currentLine.trim()) + 1;
    this.editor.setSelection({
      startLineNumber: currentAST.sLine,
      // startColumn: colStart,
      startColumn: currentAST.sColumn,
      endLineNumber: currentAST.eLine,
      // endColumn: colStart + trimmedLine.length
      endColumn: currentAST.eColumn,
    });

    if (this.codeLens) {
      this.codeLens.dispose();
    }

    // clear all previous timeouts
    let id = window.setTimeout(function() {}, 0);
    while (id--) {
      window.clearTimeout(id); // will do nothing if no timeout with id is present
    }
    setTimeout(() => {
      this.codeLens = monaco.languages.registerCodeLensProvider('cpp', {
        provideCodeLenses: (model: any, token: any) => {
          return [
            {
              range: {
                startLineNumber: currentAST.sLine,
                endLineNumber: currentAST.eLine,
                startColumn: currentAST.sColumn,
                endColumn: currentAST.eColumn,
              },
              id: `codeLens_${currentAST.Identifier}`,
              command: {
                title: `${currentAST.Identifier} = ${currentValue
                  .find(x => x.name === currentAST.Identifier)
                  .value.toString()}`,
                id: null,
              },
            },
          ];
        },
        resolveCodeLens: function({  }: any, codeLens: any, {  }: any) {
          return codeLens;
        },
      });
    },
               1500);
  }

  onRunClick(): void {
    if (this.isWorkspace()) {
      return;
    }
    let completedTasks = 0;
    for (let task of this.state.challenge.tasks) {
      const evalResult = evaluateTask(
        task,
        this.state.code,
        this.state.challenge
      );
      let evalText = `Scor: ${evalResult.score}%`;
      this.setCodeLensTextOnFunction(task.functionName, evalText);

      this.setState({
        counterExamples: evalResult.counterExamples.splice(
          0,
          Math.min(3, evalResult.counterExamples.length)
        ),
      });

      if (
        this.state.consoleInput.length === 0 &&
        evalResult.counterExamples.length > 0
      ) {
        this.setConsoleInput(evalResult.counterExamples[0].testInput);
      }
      if (evalResult.score === 100) {
        completedTasks++;
      } else {
        break;
      }
    }

    this.setState({
      currentTaskIndex: completedTasks,
    });
  }

  findFunctionStartLine(functionName: string): monaco.Range {
    let model: monaco.editor.IEditableTextModel = this.editor.getModel();
    let regex = `([^A-Za-z0-9]+)[\\n\\r\\s]*${functionName}[\\n\\r\\s]*\\([^]*\\)[\\n\\r\\s]*{[^}]*}`;
    let matches: monaco.editor.FindMatch[] = model.findMatches(
      regex,
      true,
      true,
      true,
      null,
      false
    );

    if (matches.length === 0) {
      return null;
    }

    return matches[0].range;
  }

  render() {
    const options = {
      automaticLayout: true,
      contextmenu: false,
      fontFamily: 'Fira Code, monospace',
      // fontFamily: 'Consolas, "Courier New", monospace',
       fontWeight: 'normal',
      // fontWeight: 300,
      // fontSize: 16,
      hideCursorInOverviewRuler: true,
      scrollbar: {
        vertical: 'hidden',
      },
      cursorStyle: 'block',
      autoIndent: true,
      formatOnType: true,
      occurrencesHighlight: false,
      multiCursorModifier: false,
      selectionHighlight: false,
        fontLigatures: true
    };
    const requireConfig = {
      url: `/js/monaco/require.min.js`,
      paths: {
        vs: `/js/monaco/vs`,
      },
    };
    return (
      <div
        style={{
          height: '100vh',
          overflow: 'hidden',
          maxWidth: 'unset',
          padding: 0,
          background: AppStyle.white,
        }}
      >
        <Navbar />
        <div
          className="container"
          style={{
            overflow: 'hidden',
            margin: 0,
            padding: 0,
            display: 'inline-block',
            width: '100%',
            height: '100%',
            maxWidth: 'unset',
            background: AppStyle.purple,
          }}
        >
          <SplitPane
            split="vertical"
            minSize={300}
            defaultSize={this.isWorkspace() ? '100%' : '300px'}
            style={{ height: 'calc(100% - 60px)' }}
          >
            {!this.isWorkspace() && (
              <TaskPanel
                challenge={this.state.challenge}
                taskIndex={this.state.currentTaskIndex}
                counterExamples={this.state.counterExamples}
                editor={this}
              />
            )}
            <SplitPane className="editor-pane" split="vertical" minSize={600} >
              <div className="h-100 p-3 editor-pane">
                <h2 className="pl-3" style={{ color: AppStyle.white }}>
                  code editor
                </h2>
                <MonacoEditor
                  width="100%"
                  height="93%"
                  language="cpp"
                  value={this.state.code}
                  options={options}
                  onChange={() => this.onChange()}
                  requireConfig={requireConfig}
                  editorDidMount={this.editorDidMount}
                />
                {!this.isWorkspace() &&
                  !this.state.errorMessage && (
                    <button
                      onClick={() => this.onRunClick()}
                      className="btn btn-1 btn-success"
                      style={{
                        borderRadius: '2em',
                        border: 'none',
                        position: 'relative',
                        bottom: '4em',
                        left: '2em',
                        background: AppStyle.greenGradient,
                        lineHeight: '0.5rem',
                      }}
                    >
                      <i className="fas fa-play" /> Rulează
                    </button>
                  )}
                {!this.isWorkspace() &&
                  !this.state.errorMessage && (
                    <button
                      className="btn btn-1 btn-success p-2 ml-1"
                      style={{
                        borderRadius: '50%',
                        border: 'none',
                        position: 'relative',
                        bottom: '4em',
                        left: '2em',
                        background: AppStyle.redGradient,
                        lineHeight: '0.5rem',
                      }}
                    >
                      <i className="fas fa-undo mr-0" />
                    </button>
                  )}
                {this.state.errorMessage && (
                  <button
                    className="btn btn-1 btn-success p-2 ml-1"
                    style={{
                      borderRadius: '2em',
                      border: 'none',
                      position: 'relative',
                      bottom: '4em',
                      left: '50%',
                      lineHeight: '0.5rem',
                      transform: 'translateX(-50%)',
                      background: AppStyle.purple,
                    }}
                  >
                    <i className="fas fa-info-circle mr-0" />
                    {this.state.errorMessage}
                  </button>
                )}
                {!this.state.errorMessage && (
                  <button
                    className="btn btn-1 btn-success p-2 ml-1"
                    style={{
                      borderRadius: '50%',
                      border: 'none',
                      position: 'relative',
                      bottom: '4em',
                      background: AppStyle.white,
                      float: 'right',
                      lineHeight: '0.5rem',
                    }}
                  >
                    <i
                      className="fas fa-cog mr-0"
                      style={{ color: AppStyle.darkGray }}
                    />
                  </button>
                )}
              </div>
              <LivePanel
                ref={livePanel => (this.livePanel = livePanel)}
                onSliderChanged={sliderIndex => {
                  this.setState({ sliderIndex: sliderIndex });
                  this.highlightCurrentLine();
                }}
                onConsoleInputChanged={consoleInput => {
                  this.setState({ consoleInput: consoleInput }, () =>
                    this.onChange()
                  );
                }}
                consoleOutput={this.state.JSCPPResult.output}
                variables={
                  this.state.JSCPPResult.compileError
                    ? []
                    : this.updateCurrentVariableStates(
                        this.state.prevVariables,
                        this.state.JSCPPResult.variableStates[
                          this.state.sliderIndex
                        ]
                      )
                }
                sliderLength={this.state.JSCPPResult.variableStates.length - 1}
              />
            </SplitPane>
          </SplitPane>
        </div>
      </div>
    );
  }
}

export default Editor;
