import * as React from 'react';
import AppStyle from '../AppStyle';
import { VariableState } from '../models';
import VariableBox from './VariableBox';
import ArrayBox from './ArrayBox';
import ConsolePanel from './ConsolePanel';

interface LivePanelProps {
  variables: VariableState[];
  sliderLength: number;
  onSliderChanged: (sliderValue: number) => void;
  onConsoleInputChanged: (consoleInput: string) => void;
  consoleOutput: string;
}

class LivePanel extends React.Component<LivePanelProps, {}> {

  slider: HTMLInputElement;
  consolePanel: ConsolePanel;

  handleNext() {
    this.slider.stepUp();
    this.props.onSliderChanged(Number(this.slider.value));
  }

  handlePrev() {
    this.slider.stepDown();
    this.props.onSliderChanged(Number(this.slider.value));
  }

  setCurrentIdentifier(identifier: string) {
    this.setState({currentIdentifier: identifier});
  }

  setConsoleInput(consoleInput: string) {
    this.consolePanel.inputArea.value = consoleInput;
  }

  render() {
    return (
      <div
        className="h-100 p-3 live-panel"
        style={{
        overflowY: 'auto',
        overflowX: 'hidden',
        fontSize: '1.25em',
        color: AppStyle.white,
        fontWeight: 400,
        textAlign: 'center',
        minWidth: 350,
        background: AppStyle.purple
      }}
      >
        <h2
          className="pl-3 text-left"
          style={{
            color: AppStyle.white
          }}
        >
          visual debugger
        </h2>
        <ConsolePanel 
          ref={elem => this.consolePanel = elem}
          onConsoleInputChanged={
            (consoleInput) => this.props.onConsoleInputChanged(consoleInput)
          }
          consoleOutput={this.props.consoleOutput}
        />
        <div
          className="bg-white rounded mt-3"
          style={{height: 'calc(100% - 225px)', overflowY: 'auto', overflowX: 'hidden'}}
        >
          <div
            className="w-100 d-flex align-items-center justify-content-center p-3"
            style={{
              margin: 0
            }}
          >
            <button className="ml-2 mr-2" style={buttonStyle} onClick={() => this.handlePrev()}>
              <i className="fas fa-chevron-circle-left" style={iconStyle}/>
            </button>
            <input
              type="range"
              step="1"
              className="shadow-sm"
              ref={(slider) => this.slider = slider}
              max={this.props.sliderLength}
              style={{
                borderRadius: '1em'
              }}
              onChange={() => this.props.onSliderChanged(Number(this.slider.value))}
            />
            <button className="ml-2 mr-2" style={buttonStyle} onClick={() => this.handleNext()}>
              <i className="fas fa-chevron-circle-right" style={iconStyle}/>
            </button>
          </div>
          {
            this.props.variables && this.props.variables
            .filter(x => x.hasBasicType() || x.isArray()) // filter any other objects
            .map((v, i) => v.isArray()
              ? <ArrayBox variableName={v.name} variableValue={v.value} key={i} colorOrder={i} />
              : <VariableBox
                  variableName={v.name}
                  variableValue={v.value}
                  key={i}
                  colorOrder={i}
                  isCurrentIdentifier={
                    false /* this.state.currentIdentifier === v.name */ 
                  } 
              />
            )
          }
        </div>
      </div>
    );
  }
}

const buttonStyle = {
  background: 'transparent',
  border: 'none',
  padding: 0
};

const iconStyle = {
  fontSize: '1em',
  color: AppStyle.purple
};

export default LivePanel;
