import * as React from 'react';
import './ProgressPage.css';
import { Link } from 'react-router-dom';
import Navbar from '../components/Navbar';
import AppStyle from '../AppStyle';
import { Challenge } from '../models';
import {
    VerticalTimeline,
    VerticalTimelineElement
} from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';

const Icon = (props) => {
    return (
        <i
            className={props.className}
            style={{
                display: 'block',
                width: '24px',
                height: '24px',
                position: 'relative',
                left: '50%',
                top: '50%',
                marginLeft: '-12px',
                marginTop: '-12px',
                fontSize: '24px',
                textAlign: 'center'
            }}
        />
    );
};
interface ProgressPageState {
    challenges: Challenge[];
}
class ProgressPage extends React.Component<{}, ProgressPageState> {
    constructor(props: any) {
        super(props);
        this.state = {
            challenges: []
        };
    }

    componentWillMount() {
        // TODO: do the react rotuer way
        if (!(this.props as any).location.state) {
            window.location.href = '../';
        }
        fetch(`${process.env.REACT_APP_HOST_NAME}/challenges`, {
            method: 'GET',
            headers: {
              'content-type': 'application/json',
              'Access-Control-Allow-Origin': 'localhost:8000',
              'Authorization': (this.props as any).location.state.token
            },
            mode: 'cors'
          }).then(data => data.json()).then(data => this.setState({challenges: data}));
    }
    render() {
        return (
        <div>
            <Navbar />
            <VerticalTimeline>
                <VerticalTimelineElement
                    className="vertical-timeline-element"
                    iconStyle={{ background: AppStyle.colorB, color: AppStyle.white }}
                    icon={<Icon className="fas fa-terminal" />}
                >
                    <h3 className="vertical-timeline-element-title">Workspace interactiv C++</h3>
                    <p>
                        Scrie propriile programe într-un mediu vizual.
                        <Link to={{pathname: '/workspace', state: {challenges: null}}}>
                            <button
                                className="btn btn-primary rounded shadow-sm"
                                style={{
                                    fontWeight: 600,
                                    maxWidth: '8em',
                                    margin: '0.5em'
                                }}
                            >
                                Începe <i className="fas fa-play" />
                            </button>
                            
                        </Link>
                    </p>
                </VerticalTimelineElement>
                <VerticalTimelineElement
                    className="vertical-timeline-element"
                    iconStyle={{ background: AppStyle.colorA, color: AppStyle.white }}
                    icon={<Icon className="fas fa-graduation-cap" />}
                >
                <h3 className="vertical-timeline-element-title">Învață informatică</h3>
                <p>
                    Informatică de la zero. Recomandat pentru cei ce încep.
                    <Link
                        to={{pathname: '/rezolva',
                        state: {challenges: this.state.challenges.filter(x => x.difficulty <= 10)}}}
                    >
                        <button
                            className="btn btn-primary rounded shadow-sm"
                            style={{
                                fontWeight: 600,
                                maxWidth: '8em',
                                margin: '0.5em'
                            }}
                        >
                            Începe <i className="fas fa-play" />
                        </button>
                        
                    </Link>
                </p>
                </VerticalTimelineElement>
                <VerticalTimelineElement
                    className="vertical-timeline-element--work"
                    iconStyle={{color: AppStyle.white, background: 'rgb(200, 137, 24)'}}
                    icon={<Icon className="fas fa-trophy" />}
                >
                <h3 className="vertical-timeline-element-title">Divizia Bronze</h3>
                <p>Probleme pentru cei care cunosc bazele.</p>
                <Link
                    to={{
                        pathname: '/rezolva',
                        state: {challenges: this.state.challenges.filter(x => x.difficulty > 10 && x.difficulty <= 30)}
                    }}
                >
                    <button
                        className="btn btn-primary rounded shadow-sm"
                        style={{
                            fontWeight: 600,
                            maxWidth: '8em',
                            margin: '0.5em'
                        }}
                    >
                        Începe <i className="fas fa-play" />
                    </button>
                </Link>
                </VerticalTimelineElement>
                <VerticalTimelineElement
                    className="vertical-timeline-element--work"
                    iconStyle={{ background: 'rgb(174, 177, 198)', color: AppStyle.white}}
                    icon={<Icon className="fas fa-lock" />}
                >
                <h3 className="vertical-timeline-element-title">Divizia Silver</h3>
                <p>Probleme de nivel mediu, rezolvarea lor necesită timp.</p>
                </VerticalTimelineElement>
                <VerticalTimelineElement
                    className="vertical-timeline-element--work"
                    iconStyle={{ background: 'rgb(255, 225, 86)', color: AppStyle.white }}
                    icon={<Icon className="fas fa-lock" />}
                >
                <h3 className="vertical-timeline-element-title">Divizia Gold</h3>
                <p>Probleme dificile, sunt bune pentru pregătirea de olimpiadă.</p>
                </VerticalTimelineElement>
                <VerticalTimelineElement
                    className="vertical-timeline-element"
                    iconStyle={{ color: AppStyle.white, background: 'lightblue' }}
                    icon={<Icon className="fas fa-lock" />}
                >
                <h3 className="vertical-timeline-element-title">Divizia Diamond</h3>
                <p>
                    Probleme foarte grele, doar pentru cei care merg la olimpiadă și vor să facă performanță.
                </p>
                </VerticalTimelineElement>
            </VerticalTimeline>
        </div>);
    }
}

export default ProgressPage;
