import 'rc-steps/assets/index.css';
import 'rc-steps/assets/iconfont.css';
import * as React from 'react';
import { Challenge, CounterExample } from '../models';
import { Description, CompletedStep, ActiveStep, Example, ChallengeCompleted } from './TaskComponents';
import { Tabs, Tab, TabPanel, TabList } from 'react-web-tabs';
import 'react-web-tabs/dist/react-web-tabs.css';
import Editor from './Editor';

interface TaskPanelProps {
    taskIndex: number;
    challenge: Challenge;
    counterExamples: CounterExample[];
    editor: Editor;
}

class TaskPanel extends React.Component <TaskPanelProps, {}> {
    constructor(props: TaskPanelProps) {
        super(props);
    }

    render() {
        return (
            <div
                className="col-12 border-right p-1"
                style={{
                    height: '100%',
                    padding: 0,
                    overflow: 'auto',
                    background: 'linear-gradient(to right, #f3f3f3, #e4e4e4)'
                }}
            >
                <Tabs
                    defaultTab="one"
                >
                    <TabList className="text-center" style={{fontFamily: 'Nunito !important'}}>
                        <Tab tabFor="one">Enunț</Tab>
                        <Tab tabFor="two">Alte probleme</Tab>
                    </TabList>
                    <TabPanel tabId="one">
                        <Description>
                            {this.props.challenge.description}
                        </Description>
                        {
                            this.props.challenge.tasks.filter(
                                (x, i) => i < this.props.taskIndex
                            ).map((x, i) => (
                                    <CompletedStep key={i}>
                                        {x.description}
                                    </CompletedStep>
                                )
                            )
                        }
                        {
                            this.props.taskIndex < this.props.challenge.tasks.length &&
                            <ActiveStep>
                                {this.props.challenge.tasks[this.props.taskIndex].description}
                            </ActiveStep>
                        }
                        {
                            this.props.counterExamples.map((x, index) =>
                                <Example
                                    counterExample={x}
                                    key={index}
                                    onClick={
                                        (counterExample) => this.props.editor.setConsoleInput(counterExample.testInput)
                                    }
                                />
                            )
                        }
                        {
                            this.props.taskIndex === this.props.challenge.tasks.length &&
                            <ChallengeCompleted>
                                Ai rezolvat problema, felicitări!
                            </ChallengeCompleted>
                        }
                    </TabPanel>
                    <TabPanel tabId="two">
                        <p>Tab 2 content</p>
                    </TabPanel>
                </Tabs>
            </div>
        );
    }
}

export default TaskPanel;