import { getConsoleOutput, getCPPFunctionSignature, FunctionParameter } from './JSCPPUtils';
import { Challenge, Task, TaskEvalResult } from '../models';

const jsc = require('jsverify');

function encodeJsMatrixToString(matrix: any[][]): string {
    let n = matrix.length;
    let m = matrix[0].length;
    let strOut = `${n} ${m}\n`;
    for (let i = 0; i < n; i++) {
        let row = [];
        for (let j = 0; j < m; j++) {
            row.push(matrix[i][j].toString());
        }
        strOut += row.join(' ');
        strOut += '\n';
    }
    return strOut;
}

function encodeJsVariableToString(variable: any): string {
    if (Array.isArray(variable)) {
        // check for 2D arrays
        if (variable.length > 0 && Array.isArray(variable[0])) {
            return encodeJsMatrixToString(variable);
        }
        let elements = variable.map(x => encodeJsVariableToString(x)).join(' ');
        return `${variable.length}\n${elements}\n`;
    }
    return variable.toString();
}

function encodeGeneratedVariablesToText(variables: any[]): string {
    let output = '';
    let leftPad = output.length > 0 ? ' ' : '';
    for (let variable of variables) {
        leftPad = output.endsWith('\n') ? '' : leftPad;
        output += leftPad + encodeJsVariableToString(variable);
    }
    return output;
}

function generateTestVariables(tests: number, ...args: any[]): any[] {
    let testInputs = [];
    let mockPredicate = function (...variables: any[]) {
        testInputs.push(variables);
        return true;
    };
    let mockChecks = jsc.forall(...args.concat(mockPredicate));
    jsc.check(mockChecks, {tests: tests, quiet: true});
    return testInputs;
}
function generateConsoleInputFromTestVariables(testInputs: any[]): string[] {
    return testInputs.map(
        x => encodeGeneratedVariablesToText(x)
    );
}

function generateMainTestingCode(functionName: string, source: string) {
    let header = getCPPFunctionSignature(functionName, source);
    let generateId = () => Math.random().toString(36).substr(2, 5);
    let generateCodeForAtomicVariable = (varName: string, type: string): string => {
        return `${type} ${varName};\ncin >> ${varName};`; // e.g. int x; cin >> x;
    };
    let lines = [];
    let varNames = header.parameters.map(x => `${x.parameterName}__${generateId()}__`);
    for (let i = 0; i < header.parameters.length; i++) {
        let param = header.parameters[i];

        if (param.dimensions === 0) {
            lines.push(generateCodeForAtomicVariable(varNames[i], param.parameterType));
            continue;
        }

        // we define first the variables which come for dimensions
        // just after the multidimensional variable, the next ones are dimension's specifiers
        for (let j = 1; j <= param.dimensions; j++) {
            lines.push(generateCodeForAtomicVariable(varNames[i + j], header.parameters[i + j].parameterType));
        }
        
        let primitiveType = param.parameterType.slice(0, param.parameterType.indexOf('['));
        let definitionStr = `${primitiveType} ${varNames[i]} `;
        for (let j = 1; j <= param.dimensions; j++) {
            definitionStr += `[${varNames[i + j]}]`;
        }
        definitionStr += ';';

        let consoleInputStr = '';
        let iterators = [];
        for (let j = 1; j <= param.dimensions; j++) {
            let it = `__${generateId()}__`;
            iterators.push(it);
            consoleInputStr += `\tfor (int ${it} = 0; ${it} < ${varNames[i + j]}; ${it}++)`;
        }
        consoleInputStr += `\tcin >> ${varNames[i]}${iterators.map(x => `[${x}]`).join()};`;
        lines.push(definitionStr);
        lines.push(consoleInputStr);
        i += param.dimensions;
    }
    lines.push(`cout << ${functionName}(${varNames.join(',')}) << '\\n';`);
    return `
        int main() {
            int __tests__;
            cin >> __tests__;

            for (int __test__ = 1; __test__ <= __tests__; __test__++) {
                ${lines.join('\n')}
            }

            return 0;
        }
    `;
}

function replaceMainCode(cppSource: string, newMainCode: string): string {
    let mainMatch = cppSource.match(
        /int[\n\r\s]+main[\n\r\s]*\([\n\r\s]*\)[\n\r\s]*{[^]*(return[\n\r\s]+0;)[^]*}/gm
    );
    if (!mainMatch) {
        return null;
    }
    const mainCode: string = mainMatch[0];
    return cppSource.replace(mainCode, newMainCode);
}

function getEvaluationOutput(task: Task, cppSource: string, testInput: string): string[] {
    const newMain = generateMainTestingCode(task.functionName, cppSource);
    const cppTestSource = replaceMainCode(cppSource, newMain);
    return getConsoleOutput(cppTestSource, testInput).split('\n').map(x => x.trim());
}

function variablesToString(jsVariables: any[], parameters: FunctionParameter[]): string {
    let variablesString = '';
    let paramIndex = 0;
    for (let variable of jsVariables) {
        variablesString += `${parameters[paramIndex].parameterName} = ${JSON.stringify(variable)} `;
        let dimensions = parameters[paramIndex].dimensions;
        paramIndex++;
        if (paramIndex >= parameters.length) {
            continue;
        }
        let lowerDimObj = variable;
        for (let dim = 0; dim < dimensions; dim++) {
            variablesString += `${parameters[paramIndex].parameterName} = ${JSON.stringify(lowerDimObj.length)} `;
            lowerDimObj = lowerDimObj[0];
            paramIndex++;
        }
    }
    return variablesString;
}

function evaluateTask(task: Task, cppSource: string, challenge: Challenge): TaskEvalResult {
    const tests = task.tests ? task.tests : 100;
    const testInputs = generateTestVariables(tests,  ...task.arbitraries);
    const testConsoleInputs = generateConsoleInputFromTestVariables(testInputs);
    // we add on the first line the number of tests, usually it's 100
    const testConsoleInput = [tests.toString()].concat(testConsoleInputs).join('\n');
    const currentOutput = getEvaluationOutput(task, cppSource, testConsoleInput);
    const solutionOutput = getEvaluationOutput(task, challenge.cppSolution, testConsoleInput);
    const parameters = getCPPFunctionSignature(task.functionName, cppSource).parameters;

    let passedTests = 0;
    let counterExamples = [];

    for (let i in currentOutput) {
        if (currentOutput[i].length === 0) {
            continue;
        }
        if (currentOutput[i] === solutionOutput[i]) {
            passedTests++;
        } else {
            counterExamples.push({
                testInput: testConsoleInputs[i],
                expected: solutionOutput[i],
                current: currentOutput[i],
                counterExampleStr: variablesToString(testInputs[i], parameters)
            });
        }
    }

    return {
        score: passedTests * 100 / tests,
        counterExamples: counterExamples
    };
}

export {
    encodeJsVariableToString,
    encodeGeneratedVariablesToText,
    generateConsoleInputFromTestVariables,
    generateMainTestingCode,
    evaluateTask
};