const jsc = require('jsverify');

export const Digit = jsc.suchthat('nat', function(x: number) {
    return x >= 0 && x <= 9;
});

export const NonEmptyArray = jsc.suchthat('array integer', function(v: number[]) {
    return v.length >= 2;
});

export const SquaredMatrix = jsc.suchthat('array (array integer)', function (v: number[][]) {
    let n = v.length;
    if (n !== 2) {
        return false;
    }
    for (let x of v) {
        if (x.length !== n) {
            return false;
        }
    }
    return true;
});