import * as React from 'react';
import AppStyle from '../AppStyle';
import { Animated } from 'react-animated-css';

interface Props {
    variableName: string;
    colorOrder: number;
    variableValue: number | string | boolean | null | undefined;
    indexLabel?: number | string;
    isCurrentIdentifier?: boolean;
}

interface State {
    isComponentVisible: boolean;
    isBoxVisible: boolean;
}

class VariableBox extends React.Component<Props, State> {
    static defaultProps = {
        isCurrentIdentifier: false
    };
    static backgrounds = [
        'linear-gradient(to bottom, #4FAEB3, #357277)',
        'linear-gradient(to bottom, #7A60A5, #49326F)',
        'linear-gradient(to bottom, #A94A79, #5F336C)'
    ];

    constructor(props: Props) {
        super(props);

        this.state = {
            isComponentVisible: true,
            isBoxVisible: true
        };
    }
    triggetOutAnimation() {
        this.setState({
            isComponentVisible: false
        });
    }

    toggleAnimation() {
        this.setState({
            isBoxVisible: !this.state.isBoxVisible
        });
    }

    componentWillReceiveProps(nextProps: Props) {
        if (this.props.variableValue !== nextProps.variableValue) {
            this.toggleAnimation();
        }
    }

    componentWillUnmount() {
        setTimeout(() => this.triggetOutAnimation(), 500);
    }

    render() {
        return (
            <div
                style={{
                    display: 'inline-block',
                    textAlign: 'center',
                }}
            >
                <Animated
                    animationIn="bounceInRight"
                    animationOut="bounceOutRight"
                    isVisible={this.state.isComponentVisible}
                >
                    <Animated
                        animationIn="flipInX"
                        animationOut="flipInY"
                        animateOnMount={false}
                        isVisible={this.state.isBoxVisible}
                    >
                        <div
                            className="shadow rounded"
                            style={{
                                display: 'inline-block',
                                minWidth: '3.5rem',
                                height: '3.5rem',
                                lineHeight: 'unset',
                                verticalAlign: 'middle',
                                padding: '1rem',
                                marginLeft: 0,
                                marginRight: 0,
                                color: '#FBF9FA',
                                borderImageSlice: 1,
                                margin: '0.5rem',
                                textAlign: 'center',
                                fontWeight: 'bold' as 'bold',
                                fontSize: '1rem',
                                background: VariableBox.backgrounds[
                                    this.props.colorOrder % VariableBox.backgrounds.length
                                ]
                            }}
                        >
                            {`${
                                this.props.variableValue
                                }`}
                        </div>
                    </Animated>
                    <div
                        style={{
                            color: AppStyle.darkBlue,
                            fontWeight: 'bold' /* this.props.isCurrentIdentifier ? 'bold' : 'normal' */
                        }}
                    >
            <span
                className="box-label btn p-1 shadow-sm"
                style={{
                    borderRadius: '2em',
                    lineHeight: '0.5',
                    color: AppStyle.gray,
                    background: AppStyle.white,
                    fontSize: '0.75em',
                    marginTop: '-3rem'
                }}
            >
              {this.props.variableName}<sub>{this.props.indexLabel}</sub>
            </span>
                    </div>
                </Animated>
            </div>
        );
    }
}

export default VariableBox;