import * as React from 'react';
// import {Table, Thead, Tr, Td, Th} from 'reactable';
import {Link} from 'react-router-dom';
import {Challenges} from '../challenges';
import StarsRating from 'react-stars-rating';

class ChallengesTable extends React.Component < {}, {} > {
    render() {
        console.log(Challenges);
        return (

            <table
                className="table table-bordered"
                /* sortable={true} 
                itemsPerPage={20}*/ >
                <thead>
                    <th /*column="title"*/>
                        Problema
                    </th>
                    <th /*column="difficulty"*/>
                        Dificultatea
                    </th>
                    <th /*column="tags"*/>
                        Categorii
                    </th>
                </thead>
                {Challenges.map((x, i) => {
                    return (
                        <tr key={i}>
                            <td /*column="title"*/>
                                <Link
                                    to={'rezolva/' + x
                                    .title
                                    .toLowerCase()
                                    .replace(' ', '-')}>
                                    {x.title}
                                </Link>
                            </td>

                            <td /*column="difficulty"*/>
                                <StarsRating rating={x.difficulty}/>
                            </td>

                            <td /*column="tags"*/>
                                <div>
                                    {x
                                        .tags
                                        .map((tag, j) => <label className="badge badge-info m-1" key={j}>
                                            {tag}
                                        </label>)
                                    }
                                </div>
                            </td>
                        </tr>
                    );
                })}
            </table>
        );
    }
}

export default ChallengesTable;