import * as React from 'react';
import VariableBox from './VariableBox';

interface ArrayBoxProps {
  variableName: string;
  variableValue: string;
  colorOrder: number;
}

class ArrayBox extends React.Component<ArrayBoxProps, {}> {

  render() {
    return (
      <div
        style={{
          display: 'inline-block'
        }}
      >
        {
          this.props.variableValue
          .slice(1, -1) // remove [arr] or "arr"
          .split(this.props.variableValue[0] === '[' ? ',' : '') // check if is char array or not
          .map((element, index) => {
            return (
              <VariableBox
                key={index}
                variableName={this.props.variableName}
                variableValue={element}
                indexLabel={index}
                colorOrder={this.props.colorOrder}
              />
            );
          })
        }
      </div>
    );
  }
}

export default ArrayBox;
