import * as React from 'react';
import { AuthContainer, NoAccountYet } from './AuthContainer';
import { RouteComponentProps } from 'react-router-dom';
// import Strapi from 'strapi-sdk-javascript';
// const strapi = new Strapi('http://localhost:1337', {}, {});

interface LoginState {
  errorMessage: string;
}

class Login extends React.Component<RouteComponentProps, LoginState> {
  
  emailInput: HTMLInputElement;
  passwordInput: HTMLInputElement;

  constructor(props: {}) {
    super(props);
    
    this.state = {
      errorMessage: ''
    };
  }
  
  componentDidMount() {
    this.emailInput.value = localStorage.getItem('email');
    this.passwordInput.value = localStorage.getItem('password');
  }
  
  async handleLogin(event: React.MouseEvent <HTMLButtonElement>) {
    event.preventDefault();
    /* strapi.login(this.emailInput.value, this.passwordInput.value).then(user => {
      this.props.history.push('/dashboard');
    }).catch(err => this.setState({ errorMessage: err.toString()})); */
  }
  
  render() {
    return (
      <AuthContainer>
        <form className="w-100">
          <div className="form-group">
            <input
              type="email"
              className="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              placeholder="Adresa de emaill"
              style={inputStyle}
              minLength={6}
              ref={(emailInput) => this.emailInput = emailInput}
            />

            <input
              type="password"
              className="form-control"
              id="exampleInputPassword1"
              placeholder="Parola"
              style={inputStyle}
              minLength={6}
              ref={(passwordInput) => this.passwordInput = passwordInput}
            />
            <small className="form-text text-muted">
              {this.state.errorMessage}
            </small>
          </div>
          <button
            className="btn btn-primary w-100"
            onClick={(event) => this.handleLogin(event)}
            style={{
              fontWeight: 600
            }}
          >
            Logare
          </button>
          <NoAccountYet/>
        </form>
      </AuthContainer>
    );
  }
}

const inputStyle = {
  marginTop: '0.5em'
};

export default Login;